.about {
  &__container {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: var(--mb-4);
    text-align: center;
  }

  &__text {
    margin-bottom: var(--mb-1);

    &--center {
      margin: 0 auto var(--mb-3);
      max-width: 1200px;
      font-weight: var(--medium-font);
      text-align: center;
    }
  }

  &__subtitle {
    margin-bottom: var(--mb-0-5);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--mb-1);
    margin-bottom: var(--mb-2);

    &-title {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 5px;
      margin-bottom: var(--mb-0-75);
      font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
      color: var(--dark-color);
      line-height: var(--line-height);
      font-variant-numeric: tabular-nums;
    }

    &-subtitle {
      margin-bottom: 5px;

      font-weight: var(--semibold-font);
    }

    &-text {
      color: var(--gray-color);
      line-height: var(--line-height);
    }
  }

  small {
    display: block;
    margin-bottom: var(--mb-1);
    font-size: 12px;
    color: var(--gray-color);
  }

  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: var(--mb-1);
  }

  th,
  td {
    border: 1px solid #000;
    padding: 10px;
    text-align: center;
  }

  th {
    background-color: var(--dark-color);
    color: #fff;
  }

  @media (max-width: 768px) {

    table,
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }

    thead {
      display: none;
    }

    tr {
      margin-bottom: 15px;
      border: 1px solid #ccc;
      border-radius: 8px;
      overflow: hidden;
    }

    td {
      display: flex;
      justify-content: space-between;
      gap: var(--mb-1);
      border: none;
      border-bottom: 1px solid #eee;
    }

    td:last-child {
      border-bottom: none;
    }

    td::before {
      content: attr(data-label);
      font-weight: bold;
      padding-right: 10px;
      color: #0c134f;
    }
  }
}


.number {
  display: inline-block;

}

.plus {
  display: inline-block;
}

@media screen and (max-width:1024px) {
  .about {
    &__container {
      grid-template-columns: 1fr;
      gap: var(--mb-3);
    }

    &__image {
      margin: auto;
      flex: 1 1 100%;
      width: 70%;
    }

    &__info {
      flex: 1 1 100%;
    }
  }
}



@media screen and (max-width:600px) {
  .about {
    &__image {
      width: 100%;
    }

    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }

}

@media screen and (max-width:375px) {
  .about {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}
