.rate {

  &__title {
    text-align: center;
  }

  &__text {
    margin: 0 auto 50px;
    max-width: 1000px;
    text-align: center;
  }

  .tab-component__buttons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .tab__info {
    padding: var(--mb-1) 0;
    border-top: 1px solid #cecece;
  }

  h4 {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);

    strong {
      color: var(--red-color);
    }
  }

}


.rate-item {
  &__title {
    margin-bottom: var(--mb-2);
  }

  &__descr {
    max-width: 1000px;
    margin-bottom: var(--mb-1);
  }

  &__link {
    span {
      position: relative;
      z-index: 2;
      font-size: clamp(1rem, 0.929rem + 0.36vw, 1.25rem);
      font-weight: var(--semibold-font);
      color: var(--blue-color);
      transition: color .2s ease-in-out;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: var(--blue-color);
      transform: translateX(-200%);
      transition: transform .2s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--orange-color);
      }

      &::before {
        transform: translateX(0);
      }
    }

    &:active {
      background-color: var(--black-color);

      span {
        color: var(--white-color);
      }

      &::before {
        transform: translateX(100%);
      }
    }
  }

}

.rate-grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: var(--mb-2);

  &__headline {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.25rem, 1.071rem + 0.89vw, 1.875rem);
    color: var(--dark-blue-color);
    line-height: var(--line-height);
    font-variant-numeric: tabular-nums;
  }

  &__subtitle {
    margin-bottom: var(--mb-0-5);
    font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
    font-weight: var(--medium-font);
    line-height: var(--line-height);
  }
}

.rate-list {
  li {
    display: flex;
    justify-content: space-between;
    padding: 10px 5px;

    strong {
      display: block;
      max-width: 30%;
      font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
      color: var(--red-color);
    }

    span {
      position: relative;
      display: block;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--semibold-font);
      max-width: 70%;
      text-transform: uppercase;
    }

    &:nth-child(odd) {
      background-color: #cecece9c;
    }
  }
}

.rate-subgrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.rate__list {
  max-width: 700px;
  margin: 0 0 var(--mb-2);

  li {
    display: flex;
    justify-content: space-between;
    gap: var(--mb-1);
    padding: 10px 5px;

    strong {
      display: block;
      max-width: 30%;
      font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
      color: var(--red-color);
    }

    span {
      position: relative;
      display: block;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      font-weight: var(--semibold-font);
      max-width: 70%;
      text-transform: uppercase;
    }

    i {
      font-weight: 400;
      text-transform: lowercase;
    }

    &:nth-child(odd) {
      background-color: #cecece9c;
    }
  }
}

.rate-col {
  &:not(:last-child) {
    margin-bottom: var(--mb-1);
  }
}

.webinar__list {
  margin-bottom: var(--mb-1);
  padding-left: var(--mb-2);
  list-style: square;

}

.rate-subtitle {
  margin-bottom: 15px;
}

@media screen and (max-width:1000px) {
  .rate {
    .tab-component__buttons {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media screen and (max-width:900px) {
  .rate-grid {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width:600px) {
  .rate-subgrid {
    grid-template-columns: 1fr;
  }

  .rate-list li {
    display: flex;
    justify-content: space-between;
  }

  .rate .tab-component__buttons {
    grid-template-columns: 1fr;
    gap: var(--mb-0-75);
  }

}
