.sponsored {
  background-color: #0d2e620d;


  &__headline {
    text-align: center;
  }

  &__title {
    margin-bottom: var(--mb-1);
  }

  &__text {
    margin: 0 auto 30px;
    max-width: 1200px;
    text-align: center;
  }

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  &__list {
    flex-direction: column;
    gap: var(--mb-1);
    padding-left: 40px;
    list-style: square;
    margin-bottom: var(--mb-1);
  }

  &__rates {
    flex-direction: column;
    &-item {
      justify-content: space-between;
      gap: var(--mb-1);
      padding: 10px 5px;
      p {
        position: relative;
        display: block;
        font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
        font-weight: var(--semibold-font);
        max-width: 70%;
        text-transform: uppercase;
      }

      span {
        display: block;
        max-width: 30%;
        font-size: clamp(1.125rem, 1.089rem + 0.18vw, 1.25rem);
        color: var(--red-color);
      }

      &:nth-child(odd) {
        background-color: #cecece9c;
      }
    }
  }

}



@media screen and (max-width:1000px) {
  .sponsored {
    &__row {
      grid-template-columns: 1fr;
    }
  }
}



