.newsletter {
  background-color: #0d2e620d;
  &__headline {
    margin-bottom: var(--mb-2);
    text-align: center;
  }

  &__text {
    margin: 0 auto 30px;
    max-width: 1200px;
    text-align: center;
  }

  &__row {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--mb-1);
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style: square;
    padding-left: 40px;
    margin-bottom: var(--mb-1);
  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.25rem, 1.107rem + 0.71vw, 1.75rem);
    line-height: var(--line-height);
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.125rem, 1.018rem + 0.54vw, 1.5rem);
    font-weight: var(--medium-font);
    line-height: var(--line-height);
    color: var(--blue-color);
  }

  &__descr {
    margin-bottom: var(--mb-1);
  }

  &__btn {
    display: flex;
    justify-content: center;
    min-width: 220px;
  }

  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: var(--mb-1);
  }

  th,
  td {
    border: 1px solid #000;
    padding: 10px;
    text-align: center;
  }

  th {
    background-color: var(--dark-color);
    color: #fff;
  }

  @media (max-width: 768px) {

    table,
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }

    thead {
      display: none;
    }

    tr {
      margin-bottom: 15px;
      border: 1px solid #ccc;
      border-radius: 8px;
      overflow: hidden;
    }

    td {
      display: flex;
      justify-content: space-between;
      gap: var(--mb-1);
      border: none;
      border-bottom: 1px solid #eee;
    }

    td:last-child {
      border-bottom: none;
    }

    td::before {
      content: attr(data-label);
      font-weight: bold;
      padding-right: 10px;
      color: #0c134f;
    }
  }
}


@media screen and (max-width:1000px) {
  .newsletter {
    &__list {
      max-width: 100%;
    }
    &__item {
      padding: var(--mb-2) 0;

      &:not(:last-child) {
        border-right: none;
        border-bottom: 1px solid #cecece;
      }
    }
    &__row {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width:600px) {
  .newsletter {
    &__img {
      width: 100%;
    }
  }
}
