.why-us {
  background-color: #0d2e620d;

  &__headline {
    text-align: center;
    margin-bottom: 30px;
  }

  &__subtitle {
    margin-bottom: var(--mb-1);
    text-align: center;
  }

  &__descr {
    margin: 0 auto;
    max-width: 1000px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--mb-1);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--mb-1);
    text-align: center;
  }

  &__icon {
    width: 50px;
    height: 50px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: transparent;
      stroke: var(--blue-color);
    }
  }

  &__title {
    font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
    color: var(--dark-blue-color);
    line-height: var(--line-height);
    font-variant-numeric: tabular-nums;
  }
}


@media screen and (max-width:500px) {
  .why-us {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}
