.social-reach {
  background-color: #0d2e620d;

  &__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-bottom: 50px;
  }

  &__headline {
    margin-bottom: 15px;
    text-align: center;
  }

  &__text {
    margin-bottom: 50px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: var(--mb-1);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    position: relative;
    overflow: hidden;


    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      width: 100%;
      background-color: var(--red-color);
      transform: translateX(-200%);
      transition: transform 0.3s ease-in-out;
    }

    &:hover::before {
      transform: translateX(0);
    }
  }

  &__icon {
    width: 50px;
    height: 50px;
    margin-bottom: var(--mb-1);
    flex-shrink: 0;
  }

  &__title {
    margin-bottom: var(--mb-1);
    font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
    color: var(--dark-blue-color);
    line-height: var(--line-height);
  }

  &__caption {
    margin-bottom: 0;
    color: var(--dark-color);
    line-height: var(--line-height);
    text-align: center;
  }
}

@media screen and (max-width:1000px) {
  .social-reach {
    &__grid {
      grid-template-columns: 1fr;
      gap: 15px;
      margin-bottom: 30px;
    }
  }
}

@media screen and (max-width:500px) {
  .social-reach {
    &__item {
      min-height: 220px;
    }
  }
}
